<template>
  <div class="subpage">
    <Row class="card-box-row">
      <Col
        v-for="item in indicators"
        :key="item.color"
        :span="24 / indicators.length"
      >
        <Indicator v-bind="item"/>
      </Col>
      <Col :lg="10">
        <Card>
          <p slot="title">企业性质分类占比</p>
          <Echart class="chart" :option="chart1"/>
        </Card>
      </Col>
      <Col :lg="14">
        <Card>
          <p slot="title">企业全镇分布</p>
          <Echart class="chart" :option="chart2"/>
        </Card>
      </Col>
      <Col :lg="12">
        <Card>
          <p slot="title">企业行业分布分析</p>
          <Echart class="chart" :option="chart3"/>
        </Card>
      </Col>
      <Col :lg="12">
        <Card>
          <p slot="title">规模以上企业数量趋势分析</p>
          <Echart class="chart" :option="chart4"/>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
import Echart from '@/components/Echart'
import Indicator from '@/components/Indicator'
import {
  createArray
} from '@/utils/common'
import {
  resolveGrowthRate
} from '@/utils/chart'
import {
  toFixed
} from '@/utils/number'

export default {
  components: {
    Echart,
    Indicator
  },
  data() {
    return {
      indicators: [
        {
          color: '#2d8cf0',
          icon: './icon/enterprise.png',
          text: '企业总数',
          value: 11188
        },
        {
          color: '#ed3f14',
          icon: './icon/tongbi.png',
          text: '同比',
          value: '2.11%'
        },
        {
          color: '#19be6b',
          icon: './icon/huanbi.png',
          text: '环比',
          value: '0.76%'
        }
      ],
      chart1: {
        series: [
          {
            name: '企业类型',
            type: 'pie',
            radius: [0, '60%'],
            label: {
              formatter: '{b}: {c} ({d}%)',
            },
            data: [
              {
                value: 4628,
                name: '私营'
              },
              {
                value: 2770,
                name: '有限公司'
              },
              {
                value: 2518,
                name: '股份制'
              },
              {
                value: 671,
                name: '合资'
              },
              {
                value: 671,
                name: '国有'
              },
              {
                value: 285,
                name: '外商'
              },
              {
                value: 290,
                name: '港澳台'
              },
              {
                value: 347,
                name: '其他'
              }
            ]
          },
        ]
      },
      chart2: () => {
        const data = createArray(15, i => +(120 * (Math.random() * 0.8 + 0.6)).toFixed())
        const sum = data.reduce((sum, num) => sum + num, 0)
        return {
          legend: {
            x: 'center',
            y: 'top',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: 20,
            right: 70,
            bottom: '3%',
            containLabel: true
          },
          yAxis: [
            {
              name: '数量',
              type: 'value',
            },
            {
              name: '占比',
              type: 'value',
            }
          ],
          xAxis: {
            type: 'category',
            data: [
              '石碣社区',
              '唐洪村',
              '水南村',
              '石碣村',
              '刘屋村',
              '横滘村',
              '鹤田厦村',
              '四甲村',
              '沙腰村',
              '梁家村村',
              '桔洲村',
              '单屋村',
              '涌口村',
              '西南村',
              '黄泗围村'
            ],
          },
          series: [
            {
              name: '数量',
              type: 'bar',
              data
            },
            {
              name: '占比',
              type: 'line',
              yAxisIndex: 1,
              data: data.map(value => toFixed(value / sum * 100, 2))
            },
          ]
        }
      },
      chart3: {
        series: [
          {
            name: '企业类型',
            type: 'pie',
            roseType: 'area',
            radius: [0, '80%'],
            label: {
              formatter: '{b}: {c} ({d}%)',
            },
            data: [
              {
                value: 196,
                name: '制造业'
              },
              {
                value: 357,
                name: '零售批发'
              },
              {
                value: 277,
                name: '制品业'
              },
              {
                value: 105,
                name: '电子'
              },
              {
                value: 299,
                name: '服装'
              },
              {
                value: 78,
                name: '互联网'
              },
              {
                value: 676,
                name: '其它'
              }
            ]
          },
        ]
      },
      chart4: () => {
        const data = createArray(6, i => (2000 * (Math.random() * (0.4 + i / 100) + 1)).toFixed())
        return {
          legend: {
            x: 'center',
            y: 'top',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: 20,
            right: 70,
            bottom: '3%',
            containLabel: true
          },
          yAxis: [
            {
              name: '数量',
              type: 'value',
            },
            {
              name: '增长率',
              type: 'value',
            }
          ],
          xAxis: {
            type: 'category',
            data: [
              '2014',
              '2015',
              '2016',
              '2017',
              '2018'
            ],
          },
          series: [
            {
              name: '数量',
              type: 'bar',
              data: data.slice(1)
            },
            {
              name: '增长率',
              type: 'line',
              yAxisIndex: 1,
              data: resolveGrowthRate(data)
            },
          ]
        }
      },
    }
  },
}
</script>

<style lang="scss" scoped>
</style>
