import {
  createArray
} from './common'

export function resolveGrowthRate(data) {
  const rate = []
  data.reduce((prev, next) => {
    rate.push((next / prev * 100 - 100).toFixed(2))
    return next
  })
  return rate
}

export function createData(num, base, randomRatio = 0.2, growthRate = 0) {
  return createArray(num, i => base * (Math.random() * (randomRatio * 2) + 1 - randomRatio) * (1 + i * growthRate))
}

export function resolvePieDataSource(source, num) {
  const ordered = source.slice().sort((a, b) => b.value - a.value)
  if(!num || ordered.length <= num) {
    return ordered
  }
  const top = ordered.slice(0, num)
  const others = ordered.slice(num).map(_ => _.value).reduce((num, count) => num + count, 0)
  return top.concat(others).sort((a, b) => b.value - a.value)
}
