/**
   * 数字千位符
   * @param {number} num
   * @return {string} 1234567 => '1,234,567'
   */
export const toThousands = num => (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')

/**
 * @param {number} num
 * @param {number} len 小数点后保留的位数（会4舍5入，而且最少保留一位有效数字）
 * @return {number}
 * (10.2468, 1) => 10.2
 * (10.2468, 2) => 10.25
 * (10.000001, 2) => 10
 * (0.000001, 2) => 0.000001
 */
export function toFixed(num, len = 0) {
  num = +num
  const multiple = Math.pow(10, len)
  if (num < 1 / multiple) return +num.toPrecision(1)
  return Math.round(num * multiple) / multiple
}

export function safeAdd(a, b) {
  const [, decimal1 = ''] = (a + '').split('.')
  const [, decimal2 = ''] = (b + '').split('.')
  const multiple = Math.pow(10, Math.max(decimal1.length, decimal2.length))
  return (a * multiple + b * multiple) / multiple
}
